const Vue = require('vue/dist/vue');
const querystring = require('querystring');
const _ = require('lodash');
const forms = require('./forms');
const {fbDB} = require('./firebase');

window.addEventListener('DOMContentLoaded', () => {

  const fieldStyleElement = document.createElement('style');
  document.head.appendChild(fieldStyleElement);
  const fieldStyleSheet = fieldStyleElement.sheet;

  new Vue({
    el: '#app',
    render(h) {
      if (!forms[this.form]) {
        return h('div', {}, ['No form found'])
      } else {
        // Group the form fields by their page
        const formFieldsByPage = _.groupBy(this.formFieldsWithRender, 'pageId')

        //
        const pages = _.keys(formFieldsByPage).map(pageId =>
          h('div', {slot: pageId, class: {'testtest': true}}, formFieldsByPage[pageId].map(ff => {
            return h(ff.component, {
              props: {
                d: this.data
              }
            })
          }))
        )

        console.log(pages)

        return h(forms[this.form].component, {}, pages)
      }
    },
    data: {
      data: null,
      form: null,
      formFields: [],
    },
    computed: {
      formFieldsWithRender() {
        while (fieldStyleSheet.cssRules.length > 0) {
          fieldStyleSheet.deleteRule(fieldStyleSheet.cssRules.length - 1);
        }

        return this.formFields && this.formFields.map(ff => {
          const autogenId = 'formfield-' + Math.floor(Math.random() * 100000000).toString(35);
          const compileResult = Vue.compile(`<div id="${autogenId}">{{${ff.params.template}}}</div>`);
          fieldStyleSheet.insertRule(`
#${autogenId} {
  position: absolute;
  left:     ${ff.params.left}px;
  top:      ${ff.params.top}px;
  height:   ${ff.params.height}px;
  width:    ${ff.params.width}px;
  font-size: ${ff.params.fontSize}px;
  font-family: sans-serif;
}`, fieldStyleSheet.cssRules.length);

          fieldStyleSheet.insertRule(`
@media print {
  #${autogenId} {
    left:     ${1.33333 * ff.params.left}pt;
    top:      ${1.33333 * ff.params.top}pt;
    height:   ${1.33333 * ff.params.height}pt;
    width:    ${1.33333 * ff.params.width}pt;
    font-size: ${1.33333 * ff.params.fontSize}pt;
  }
}`, fieldStyleSheet.cssRules.length)

          return ({
            ...ff,
            component: {
              props: ['d'],
              render: compileResult.render,
              staticRenderFns: compileResult.staticRenderFns,
              created() {
                console.log(this.d);
              }
            }
          })
        })
      }
    },
    watch: {
      form() {
        fbDB.ref(`formTemplates/${this.form}`).once('value', formFieldsRef => {
          this.formFields = _.values(formFieldsRef.val() || {})
        })
      }
    },
    created() {
      let query = window.location.hash.replace(/^#/, '');

      query = JSON.parse(querystring.parse(query).q);

      this.data = query.data;
      this.form = query.form;
    }
  })
});



