const firebase = require('firebase');

const app = firebase.initializeApp({
  apiKey: "AIzaSyAdu9gHiJFVVH-kliblU5-1sKWPTdLRU7k",
  authDomain: "echo-161401.firebaseapp.com",
  databaseURL: "https://echo-161401.firebaseio.com",
  storageBucket: "echo-161401.appspot.com",
  messagingSenderId: "808171874047"
});

const provider = new firebase.auth.GoogleAuthProvider();

module.exports = {
  fbAuth: app.auth(),
  fbDB: app.database(),
  fbStorage: app.storage(),

  fbSignIn() {
    return app.auth().signInWithPopup(provider);
  },
  fbSignOut() {
    return app.auth().signOut();
  }
};



